<template>
  <div class="product-page">
    <m-header>
      <span>S W I A S T</span>
    </m-header>
    <m-article v-if="products.length !== 0">
      <div class="images">
        <img
          v-for="img in products[i].images"
          :key="img.id"
          :src="url + 'images/' + img.filename"
          alt="house photos"
        />
      </div>
      <div class="flexy">
        <div class="infos">
          <h3>{{ products[i].title }}</h3>
          <p>{{ products[i].details }}</p>
        </div>

        <div class="contact">
          <h3>Contact Informations:</h3>
          <div v-for="(contact, app) in contactInfos" :key="contact">
            <div v-if="contact !== ''">
              <b>{{ app }}</b
              >: {{ contact }}
            </div>
          </div>
        </div>
      </div>

      <div class="tags">
        <span class="tag" v-for="tag in productTags" :key="tag">
          <span>{{ tag }}</span>
        </span>
      </div>
    </m-article>
    <h2 v-else>Sorry We are not in your country yet</h2>
    <transition name="fade-button" mode="out-in">
      <button @click="switchPages('back')" v-if="i >= 1">Previous</button>
    </transition>
    <transition name="fade-button" mode="out-in">
      <button @click="switchPages('next')" v-if="i < products.length - 1">
        Next
      </button>
    </transition>
    <m-footer />
  </div>
</template>

<script>
import axios from "axios";
import MArticle from "../components/MArticle";
import MHeader from "../components/MHeader";
import MFooter from "../components/MFooter";

export default {
  name: "Product",
  components: { MArticle, MHeader, MFooter },
  data() {
    return {
      url: this.$store.state.url,
      i: 0,
      products: [
        {
          title: "The Wonderful house",
          details: "This new house in germany is the best house there",
          country: "germany",
          tags: "Wonderful beauty nature test sea",
          contact: { " whatsapp": "0999999999", " mail": "abcd@gmail.com" },
          images: ["images/house1.jpeg", "house2.jpeg", "house3.jpg"],
        },
      ],
    };
  },
  computed: {
    productTags() {
      return this.products[this.i].tags.split(" ");
    },
    contactInfos() {
      return this.products[this.i].contact;
    },
    currentCountry() {
      return this.$store.state.currentCountry;
    },
  },
  methods: {
    switchPages(where) {
      where === "back" ? this.i-- : this.i++;
    },
    getProducts() {
      axios
        .get(this.url + this.currentCountry)
        .then((res) => {
          this.products = res.data;
          this.productTags = res.data[this.i].tags.split(" ");
          this.contactInfos = res.data[this.i].contact;
          console.log(this.contactInfos);
        })
        .catch((err) => console.log(err));
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>

